@keyframes scaleUp{0%{transform:scale(0)}50%{transform:scale(1.15)}100%{transform:scale(1)}}.wrap{padding:30px;min-height:100vh;background:linear-gradient(to right bottom, rgb(255, 251, 235), rgb(255, 237, 213))}.wrap .title{padding:20px 0 40px;text-align:center;color:#8b5a2b;font-size:45px;font-weight:bold}.wrap .infoBar{padding:20px;display:flex;justify-content:space-between;align-items:center;border-radius:10px;box-shadow:0 5px 10px rgba(0,0,0,.3);background:#fffdf8}.wrap .infoBar .player{display:flex;justify-content:center;align-items:center}.wrap .infoBar .player .piece{width:80px;height:80px;border-radius:50%;overflow:hidden;box-shadow:0 3px 3px rgba(0,0,0,.3);animation:scaleUp .3s ease-out}.wrap .infoBar .player .piece img{width:100%;height:100%;object-fit:contain}.wrap .infoBar .player .text{padding:0 15px;color:#000;font-size:24px;font-weight:normal}.wrap .infoBar .player .name{color:#8b5a2b;font-size:24px;font-weight:normal}.wrap .infoBar .btns{display:flex;justify-content:center;align-items:center;gap:20px}.wrap .infoBar .btns>span{display:flex;justify-content:center;align-items:center;width:100px;height:50px;border-radius:10px;background:#8b5a2b;color:#fff;font-size:24px;font-weight:normal}.wrap .board{padding:60px 0}.wrap .board .row{display:flex;justify-content:center;align-items:center}.wrap .board .row .cell{display:flex;justify-content:center;align-items:center;width:42px;height:42px;border-top:1px solid #8b5a2b;border-left:1px solid #8b5a2b;background:#deb887}.wrap .board .row .cell .piece{width:90%;height:90%;border-radius:50%;overflow:hidden;box-shadow:0 3px 3px rgba(0,0,0,.3);animation:scaleUp .3s ease-out}.wrap .board .row .cell .piece img{width:100%;height:100%;object-fit:contain}.wrap .board .row .cell:last-child{border-right:1px solid #8b5a2b}.wrap .board .row:last-child .cell{border-bottom:1px solid #8b5a2b}.wrap .rule{padding:20px;border-radius:10px;box-shadow:0 5px 10px rgba(0,0,0,.3);background:#fffdf8}.wrap .rule h3{color:#8b5a2b;font-size:30px;font-weight:bold}.wrap .rule ul{padding-top:10px}.wrap .rule ul li{line-height:38px;color:#000;font-size:24px;font-weight:normal}.skinModal{position:absolute;top:0;left:0;width:100vw;height:100vh;background:rgba(0,0,0,.5)}.skinModal .inner{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);padding:25px 20px;width:80%;box-sizing:border-box;border-radius:20px;background:#fff}.skinModal .inner .cls{position:absolute;top:20px;right:20px;width:30px;height:30px;background:url("./assets/cls.png");background-size:100% 100%}.skinModal .inner .title{text-align:center;color:#8b5a2b;font-size:38px;font-weight:bold}.skinModal .inner ul li{padding-bottom:30px}.skinModal .inner ul li .label{color:#8b5a2b;font-size:28px;font-weight:normal}.skinModal .inner ul li .items{padding-top:10px;display:flex;justify-content:space-between;align-items:center;flex-wrap:wrap}.skinModal .inner ul li .items .box{position:relative;width:80px;height:80px}.skinModal .inner ul li .items .box img{width:100%;height:100%;object-fit:contain}.skinModal .inner ul li .items .box.on::after{position:absolute;bottom:5px;right:5px;content:"";width:30px;height:30px;background:url("./assets/selected.png");background-size:100% 100%}